ite;\n  box-shadow: 0 2px 15px rgba(black, 0.3);\n  justify-content: flex-start;\n  border-radius: $border-radius;\n  padding: 4px;\n  font-size: 13px;\n  font-family: sans-serif;\n  line-height: 1.5em;\n\n  // reset\n  ul, li, button, form {\n    padding: 0;\n    margin: 0;\n    border: 0;\n    list-style: none;\n    outline: none;\n  }\n\n  .controls {\n    min-width: 180px;\n    margin: 4px;\n  }\n\n  .periods,\n  .ranges {\n    li {\n      @include button-sizing;\n\n      margin: 0;\n      background: $grey-color;\n      color: $active-color;\n      cursor: pointer;\n\n      &:hover,\n      &.active {\n        background: $active-color;\n        color: white;\n      }\n    }\n  }\n\n  .periods {\n    display: inline-flex;\n    margin: 0 auto 8px;\n\n    li {\n      &:first-child {\n        border-radius: $border-radius 0 0 $border-radius;\n      }\n\n      &:last-child {\n        border-radius: 0 $border-radius $border-radius 0;\n      }\n    }\n  }\n\n  .ranges {\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n\n    li {\n      border-radius: $border-radius;\n      margin-bottom: 8px;\n      text-align: left;\n    }\n  }\n\n  $m: 3px;\n\n  .custom-range-inputs {\n    display: flex;\n    margin: - $m;\n    margin-bottom: 8px - $m;\n\n    input {\n      min-width: 50px;\n      width: 50px;\n      flex: 1;\n      margin: $m;\n      border-radius: $border-radius;\n      border: 1px solid #ccc;\n      height: auto;\n      padding: 0.5em;\n      font-size: 13px;\n      color: #333;\n    }\n  }\n\n  .custom-range-buttons {\n    display: flex;\n    margin: - $m;\n\n    button {\n      @include button-sizing;\n\n      margin: $m;\n\n      border-radius: $border-radius;\n      background: $grey-color;\n      color: $active-color;\n\n      &:hover {\n        background: darken($grey-color, 10%);\n      }\n\n      &.apply-btn {\n        background: $green-color;\n        color: white;\n\n        &:hover {\n          background: darken($green-color, 10%);\n        }\n      }\n    }\n  }\n\n  .arrow-left,\n  .arrow-right {\n    display: inline-block;\n    position: relative;\n    background-color: #333;\n    width: 7px;\n    height: 3px;\n    margin-bottom: 2px;\n    vertical-align: middle;\n\n    &:before {\n      content: '';\n      display: block;\n      position: absolute;\n      border: 5px solid transparent;\n    }\n  }\n\n  .arrow-left {\n    margin-left: 5px;\n\n    &:before {\n      border-right-width: 6px;\n      border-right-color: #333;\n      transform: translate(-10px, -3.5px);\n    }\n  }\n\n  .arrow-right {\n    margin-right: 5px;\n\n    &:before {\n      border-left-width: 6px;\n      border-left-color: #333;\n      transform: translate(6px, -3.5px);\n    }\n  }\n}\n\n@import 'daterangepicker/arrows';\n@import 'daterangepicker/selects';\n@import 'daterangepicker/calendar';\n@import 'daterangepicker/modifiers';\n","@mixin arrow($size, $color, $alignment) {\n  position: absolute;\n  top: - $size;\n  #{$alignment}: 16px - $size;\n  display: inline-block;\n  border-right: $size solid transparent;\n  border-bottom: $size solid $color;\n  border-left: $size solid transparent;\n  content: '';\n}\n\n.daterangepicker.orientation-right:not(.standalone):before {\n  @include arrow(7px, rgba(0,0,0,0.2), 'left')\n}\n\n.daterangepicker.orientation-left:not(.standalone):before {\n  @include arrow(7px, rgba(0,0,0,0.2), 'right')\n}\n\n.daterangepicker.orientation-right:not(.standalone):after {\n  @include arrow(6px, #fff, 'left')\n}\n\n.daterangepicker.orientation-left:not(.standalone):after {\n  @include arrow(6px, #fff, 'right')\n}\n",".daterangepicker select {\n  width: 100%;\n  box-sizing: border-box;\n  padding: 2px 7px;\n  height: auto;\n  font-size: 13px;\n  line-height: 1.5em;\n  text-align: center;\n  margin: 0 2px;\n\n  &.hidden {\n    display: none;\n  }\n\n  &.month-select {\n    flex: 10;\n    max-width: 75%;\n  }\n\n  &.year-select {\n    flex: 11;\n    max-width: 75%;\n  }\n\n  &.decade-select